@import url("https://fonts.googleapis.com/css2?family=Epilogue:wght@500;700&display=swap");

:root {
  --Almost-White: hsl(0, 0%, 98%);
  --Medium-Gray: hsl(0, 0%, 41%);
  --Almost-Black: hsl(0, 0%, 8%);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Epilogue;
  background: var(--Almost-White);
}

li {
  list-style-type: none;
}
/* header/Navbar */
header,
ul,
.nav {
  display: flex;
}

header {
  margin: 20px;
  color: var(--Medium-Gray);
  align-items: center;
  font-weight: 500;
  cursor: pointer;
}

.logo {
  align-self: center;
  height: 20px;
}

#menu,
#close {
  display: none;
}

.nav {
  margin-left: 50px;
}

.nav li {
  margin-right: 30px;
}

.access {
  display: flex;
  margin-left: auto;
}

.access li {
  margin-left: 25px;
}

.register {
  border: 1px solid black;
  padding: 10px;
  margin-top: auto;
  border-radius: 15px;
}

.login {
  align-self: center;
}

.navitem:hover {
  color: var(--Almost-Black);
}
/* drop-down functionality */
.feature-item ul {
  position: absolute;
  flex-direction: column;
  border-radius: 5px;
  top: 60px;
  left: 110px;
  width: 110px;
  height: 110px;
  color: var(--Medium-Gray);
  background-color: white;
  box-shadow: 1px 1px 20px 0px rgb(197, 205, 209);
  padding: 10px;
  z-index: 1;
  animation: animate 1s ease-in-out;
}

@keyframes animate {
  0% {
    height: 11px;
    overflow: hidden;
  }
  100% {
    height: 110px;
    overflow: hidden;
  }
}

.feature-item ul li, .company-details ul li {
  text-align: left;
  margin: 3px;
  font-size: 13px;
  cursor: context-menu;
}

.feature-item ul li{
  display:flex;
  align-items: center;
}

.feature-item ul li img {
  padding-top: 1px;
  margin-right: 10px;
  width: 11px;
}

.company-details ul {
  position: absolute;
  flex-direction: column;
  border-radius: 5px;
  top: 60px;
  left: 250px;
  width: 90px;
  height: 100px;
  color: var(--Medium-Gray);
  background-color: white;
  box-shadow: 1px 1px 20px 0px rgb(197, 205, 209);
  padding: 10px;
  z-index: 1;
  animation: animate 1s ease-in-out;
}

/* Display section */

section {
  display: grid;
  grid-template-columns: 2fr 1fr;
  width: 80vw;
  margin: 0 auto;
}

.welcome-info {
  position: relative;
  margin-top: 30px;
  grid-column: 1;
  grid-row: span 2;
  align-self: center;
  margin-right: 150px;
}

.welcome-info h1 {
  font-size: 70px;
  margin-bottom: 20px;
}

.welcome-info p {
  color: var(--Medium-Gray);
  font-size: 18px;
  width: 350px;
}

.img {
  height: 70vh;
  width: 400px;
  background: url("./image-hero-desktop.png") no-repeat center;
  background-size: contain;
  animation: rotate 600ms ease;
}

@keyframes rotate {
  12.5%{
    transform: rotate(45deg);
    width: 50px;
  }
  25%{
    transform: rotate(90deg);
    width: 100px;
  }
  37.5%{
    transform: rotate(135deg);
    width:150px;
  }
  50%{
    transform: rotate(180deg);
    width: 200px;
  }
  62.5%{
    transform: rotate(225deg);
    width: 250px;
  }
  75%{
    transform: rotate(270deg);
    width: 300px;
  }
  87.5%{
    transform: rotate(315deg);
    width: 350px;
  }
  100%{
    transform:rotate(360deg);
    width: 400px;
  }
}

button {
  border: 1px solid black;
  border-radius: 10px;
  padding: 10px 20px;
  margin-top: 25px;
  font-weight: 700;
  color: var(--Almost-White);
  background: var(--Almost-Black);
}

button:hover {
  background-color: var(--Almost-White);
  color: var(--Almost-Black);
  border: 1px inset black;
}

.combo {
  display: grid;
  grid-row: 1/1/1;
}

.client {
  width: 70px;
  align-self: end;
  margin-right: 20px;
}

.clients {
  align-self: end;
}

.attribution {
  position: fixed;
  width: 100vw;
  bottom: 0;
  left: 50%;
  background-color: var(--Almost-White);
  transform: translate(-50%);
  font-size: 11px;
  text-align: center;
}
.attribution a {
  color: hsl(228, 45%, 44%);
}

@media (max-width: 500px) {
  .nav ul {
    position: absolute;
    flex-direction: column;
    top: 0;
    height: 100%;
    background: var(--Almost-White);
    right: 0;
    width: 65vw;
    z-index: 1;
    animation: slideOut 2s ease;
  }

  .nav ul li:first-child {
    margin-top: 100px;
    right: 150px;
  }
  .arrow {
    margin-left: 5px;
  }

  .nav ul li {
    margin-top: 20px;
    margin-left: 20px;
    font-size: 20px;
    font-weight: 700;
  }

  .access ul {
    position: absolute;
    flex-direction: column;
    z-index: 3;
    right: 25px;
    animation: slide 2s ease;
  }

  .access ul li {
    margin-top: 15px;
    font-weight: 700;
    font-size: 18px;
  }

  .register {
    width: 200px;
    text-align: center;
  }

  .feature-item ul, .company-details ul{
    top: 130px;
    width: 500px;
    left: 170px;
    box-shadow: none;
    background: transparent;
  }

  .feature-item ul li, .company-details ul li{
    font-size: 20px;
    font-weight: 700;
  }

.features:hover .feature-item{
  display: flex;
  z-index: 10;
}

  

  .company-details ul{
    top: 170px;
  }

  .feature-item ul li img {
    padding-top: 1px;
    margin-right: 10px;
    width: 20px;
  }

  #menu {
    display: block;
  }
  #logo {
    height: 30px;
  }
  .logo {
    display: flex;
    width: 100%;
    justify-content: space-between;
  }

  #close {
    position: absolute;
    z-index: 5;
    fill: var(--Medium-Gray);
    font-weight: 700;
    right: 20px;
  }

  /* Animation for sidebar*/
  @keyframes slideOut {
    0% {
      width: 10vw;
      overflow: hidden;
    }
    100% {
      width: 65vw;
      overflow: hidden;
    }
  }
  @keyframes slideIn {
    0% {
      width: 65vw;
      overflow: hidden;
    }
    100% {
      width: 10vw;
      overflow: hidden;
    }
  }

  @keyframes slide {
    0% {
      width: 0;
      overflow: hidden;
    }
    100% {
      width: 0;
      overflow: hidden;
    }
  }

  section {
    grid-template-columns: 1fr;
    margin: 0 auto;
    width: 100vw;
  }
  section .img {
    grid-row: 1;
    height: 300px;
    width: 100vw;
    margin-right: 0;
    margin-left: 0;
    margin-top: 15px;
    background: url(./image-hero-mobile.png) no-repeat center;
    background-size: contain;
  }

  .welcome-info {
    margin: 0;
    margin-top: 30px;
    margin-bottom: 20px;
    text-align: center;
    width: 100%;
  }
  .welcome-info h1 {
    font-size: 35px;
    font-weight: 700;
    margin-right: 0;
    margin-bottom: 10px;
  }

  .welcome-info p {
    width: 80vw;
    margin: 0 auto;
  }
  .combo {
    margin-left: 0;
    margin-right: 0;
  }
  .clients {
    width: 90vw;
    margin: 0 auto;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .client {
    margin-right: 10px;
  }
  .clients:last-child {
    margin-right: 0;
  }
  #dark {
    position: fixed;
    background-color: var(--Almost-Black);
    height: 100%;
    width: 100%;
    overflow: hidden;
    top: 0;
    bottom: 0;
    opacity: 0.5;
    z-index: 1;
  }
}
